
import { defineComponent, reactive } from "vue";

import { InputZonesAndRoutes, DropdownMidiEventTypeEnum } from "../../src/UserDataConfig/Easyconfig";

import CardHeader from "../a/card-header.vue";
import CardBody from "../a/card-body.vue";

import Row from "../a/row-component.vue";
import RowCell from "../a/row-cell.vue";
import RowCellRight from "../a/row-cell-right.vue";
import BtnHref from "../a/btn-href.vue";
import ServerInOutPortsSelect from "../a/serverIn-out-ports-select.vue";
import SelectedMidiEventType from "../a/selected-midi-event-type.vue";
import SelectedMidiChannel from "../a/selected-midi-channel.vue";
import SelectedMidiData from "../a/selected-midi-data.vue";

export default defineComponent({
  components: {
    SelectedMidiChannel,
    SelectedMidiEventType,
    SelectedMidiData,
    ServerInOutPortsSelect,
    CardHeader,
    CardBody,
    Row,
    RowCell,
    RowCellRight,
    BtnHref,
  },
  props: {
    inputVal: {
      type: InputZonesAndRoutes,
      required: true,
    },
  },
  setup(props) {
    const easyConfigRoutes = reactive(props.inputVal.easyConfigRoutes);
    const zoneNames = reactive(props.inputVal.zoneNames);

    function deleteSelf(idx: number) {
      easyConfigRoutes.splice(idx, 1);
    }

    /*
    function ensureNrpnWithCcOnly(idx: number){
      const isTo  =[DropdownMidiEventTypeEnum.controlchange, DropdownMidiEventTypeEnum.nrpn].
          indexOf(easyConfigRoutes[idx].
          toSelectedMidiEventTypeId) > -1
      const isFrom = [DropdownMidiEventTypeEnum.controlchange, DropdownMidiEventTypeEnum.nrpn].
          indexOf(easyConfigRoutes[idx].
          fromSelectedMidiEventTypeId) > -1

      if (isFrom !== isTo ){
            easyConfigRoutes[idx].fromSelectedMidiEventTypeId = 0;
            easyConfigRoutes[idx].toSelectedMidiEventTypeId = 0;
            
          }          
    }
    */

    function toEventTypeChanged(idx: number) {
      if (easyConfigRoutes[idx].fromSelectedMidiEventTypeId == 0) {
        easyConfigRoutes[idx].fromChannel = easyConfigRoutes[idx].toChannel;
      }
      if (easyConfigRoutes[idx].toSelectedMidiEventTypeId == 0) {
        easyConfigRoutes[idx].fromSelectedMidiEventTypeId = 0;
      }
     //ensureNrpnWithCcOnly(idx);


    }

    function fromEventTypeChanged(idx: number) {
      if (easyConfigRoutes[idx].fromSelectedMidiEventTypeId == 0) {
        easyConfigRoutes[idx].toSelectedMidiEventTypeId = 0;
      } else {
        easyConfigRoutes[idx].toSelectedMidiEventTypeId =
          easyConfigRoutes[idx].fromSelectedMidiEventTypeId;
      }
      //ensureNrpnWithCcOnly(idx);
      
    }

    function toChannelChange(idx: number) {
      if (easyConfigRoutes[idx].fromChannel == -1) {
        easyConfigRoutes[idx].fromChannel = easyConfigRoutes[idx].toChannel;
      }
      if (easyConfigRoutes[idx].toChannel == -1) {
        easyConfigRoutes[idx].fromChannel = -1;
      }
    }

    function fromChannelChange(idx: number) {
      if (easyConfigRoutes[idx].fromChannel == -1) {
        easyConfigRoutes[idx].toChannel = -1;
      } else {
        easyConfigRoutes[idx].toChannel = easyConfigRoutes[idx].fromChannel;
      }
    }

    const transposeRange = [];
    for (let i = -36; i <= 36; i++) {
      transposeRange.push(i);
    }

    return {easyConfigRoutes, zoneNames, deleteSelf, toEventTypeChanged, fromEventTypeChanged, 
            toChannelChange, fromChannelChange, transposeRange, DropdownMidiEventTypeEnum}

  },
});
