
import Row from "../a/row-component.vue";
import RowCell from "../a/row-cell.vue";
import RowCellRight from "../a/row-cell-right.vue";
import BtnHref from "../a/btn-href.vue";

import { defineComponent, computed } from "vue";
import { KeyboardZone } from "../../src/UserDataConfig/Easyconfig";

export default defineComponent({
  components: {
    Row,
    RowCell,
    RowCellRight,
    BtnHref,
  },
  props: {
    modelValue: {
      type: Number,
      required: true,
    },
    keyboardZone: {
      type: KeyboardZone,
      required: true,
    }
  },
  setup(props) {
    const noteNames = computed(() => {
      const ret: { id: number; name: string }[] = [];
      for (let i = 9; i <= 96; i++) {
        ret.push({ id: i, name: props.keyboardZone.getPositionName(i) });
      }
      return ret;
    });
    return {noteNames}; 
  },
});


