
import { defineComponent, computed, ref, reactive, nextTick } from "vue";

import * as Connection from "../../src/connection";

import Page from "../a/page-component.vue";
import RowCell from "../a/row-cell.vue";
import Btn from "../a/btn-component.vue";
import Card from "../a/card-component.vue";
import Row from "../a/row-component.vue";

import ServerInOutPortsSelect from "../a/serverIn-out-ports-select.vue";
import SplitedInputComponent from "./splited-input-component.vue";
import EasyConfigRoutesComponent from "./easy-config-routes-component.vue";

export default defineComponent({
  components: {
    Page,
    Row,
    RowCell,
    Btn,
    Card,
    ServerInOutPortsSelect,
    SplitedInputComponent,
    EasyConfigRoutesComponent,
  },
  setup() {
    const inputToAdd = ref(-1);
    const routeToAdd = ref(-1);

    const easyConfig = reactive(
      Connection.loginStatus.userDataConfig.activePreset.easyConfig
    );
    const inputZonesAndRoutes = easyConfig.inputZonesAndRoutes;

    const loginStatus = computed(() => Connection.loginStatus);

    function addRoute() {
      if (routeToAdd.value === -1) {
        return;
      }

      easyConfig.addRoute(Connection.loginStatus.inPorts[routeToAdd.value]);

      const n = routeToAdd.value;
      routeToAdd.value = -1;
      nextTick(() => {
        routeToAdd.value = n;
      });
    }

    function addSplitedKeyboard() {
      if (inputToAdd.value === -1) {
        return;
      }
      easyConfig.addKeyboardSplit(
        Connection.loginStatus.inPorts[inputToAdd.value],
        60
      );

      const n = inputToAdd.value;
      inputToAdd.value = -1;
      nextTick(() => {
        inputToAdd.value = n;
      });
    }

    return {
      loginStatus,
      inputToAdd,
      routeToAdd,
      addRoute,
      addSplitedKeyboard,
      inputZonesAndRoutes,
    };
  },
});
